@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Light.woff") format("woff"), url("../fonts/OpenSans-Light.woff") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-SemiBold.woff") format("woff"), url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open-Sans";
  src: url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.38;
  font-family: "Open-Sans", "Helvetica", sans-serif; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.page-header {
  background-color: #000; }

.page-header__wrapper {
  height: 825px;
  background: #000 url("../img/promo-bike.jpg") no-repeat;
  background-size: contain;
  background-position: center top;
  position: relative; }
  @media (max-width: 768px) {
    .page-header__wrapper {
      background: #fff url("../img/promo-bike-tablet.jpg") no-repeat; } }

.page-header__grid {
  max-width: 879px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 62px 255px 267px;
  margin: 0 auto; }
  @media (max-width: 768px) {
    .page-header__grid {
      grid-template-columns: 1fr;
      grid-template-rows: 25px 290px 350px; } }

.page-header__logo {
  grid-row: 3 / 4;
  grid-column: 1 / 2; }
  @media (max-width: 768px) {
    .page-header__logo {
      grid-row: 1; } }

.page-header__main-nav {
  grid-row: 1 / 2;
  grid-column: 1 / 4;
  padding: 0 2.75px; }
  @media (max-width: 768px) {
    .page-header__main-nav {
      grid-row: 1;
      padding: 0; } }

.page-header__title {
  grid-row: 3 / 4;
  grid-column: 1 / 3;
  margin-top: 6px; }
  @media (max-width: 768px) {
    .page-header__title {
      grid-row: 2 / 3;
      grid-column: 1;
      text-align: center;
      margin: 0 auto; } }

.page-header__feedback {
  margin-top: 6px;
  grid-row: 3 / 4;
  grid-column: 2 / 3;
  justify-self: end; }
  @media (max-width: 768px) {
    .page-header__feedback {
      margin-top: 20px;
      grid-row: 3 / 4;
      grid-column: 1;
      margin: 0 auto; } }

.main-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding-top: 14px; }
  @media (max-width: 768px) {
    .main-nav__list {
      display: none; } }

.main-nav__item {
  padding: 13px 20px; }

.main-nav__link {
  font-weight: 300;
  color: #fff;
  opacity: 0.7; }

.main-nav__toggle {
  display: none; }
  @media (max-width: 768px) {
    .main-nav__toggle {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6; } }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 18px;
  left: 50%;
  transform: translateX(-50%);
  width: 22.5px;
  height: 2px;
  background-color: #000;
  box-shadow: 0 6px 0 0 #000, 0 12px 0 0 #000; }

.main-nav--opened .main-nav__toggle {
  background: #fff; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 13px;
    transform: translateY(-50%);
    width: 26px;
    height: 2px;
    background-color: rgba(20, 21, 23, 0.2);
    z-index: 100; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }

@media (max-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: none; }
  .main-nav--nojs .main-nav__list {
    display: block; }
  .main-nav--opened .main-nav__list {
    position: absolute;
    z-index: 100;
    min-width: 100vw;
    height: 100vh;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: start; }
  .main-nav--opened .main-nav__item {
    background-color: #f6f6f6;
    margin-bottom: 40px;
    padding: 15px 20%;
    position: relative; }
    .main-nav--opened .main-nav__item:first-child {
      margin-top: 50px; }
    .main-nav--opened .main-nav__item::after {
      content: "";
      position: absolute;
      top: 50%;
      right: 10%;
      width: 12px;
      height: 12px;
      border: 3px solid #48494D;
      border-bottom: transparent;
      border-left: transparent;
      transform: translateY(-50%) rotate(45deg); }
  .main-nav--opened .main-nav__link {
    font-weight: 400;
    color: #48494D;
    font-size: 20px; } }

.logo {
  padding-top: 25px;
  padding-left: 22px; }

.title {
  max-width: 380px; }
  @media (max-width: 768px) {
    .title {
      max-width: 435px; } }

.title__title {
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  color: #fff;
  letter-spacing: -0.005em;
  margin-top: 46px;
  margin-bottom: 25px; }
  @media (max-width: 768px) {
    .title__title {
      font-size: 30px;
      line-height: 42px;
      margin-top: 85px;
      margin-bottom: 20px; } }

.title__text {
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  color: #fff;
  letter-spacing: -0.005em; }
  @media (max-width: 768px) {
    .title__text {
      font-size: 18px; } }

.feedback {
  max-width: 327px; }
  @media (max-width: 768px) {
    .feedback {
      max-width: 324px; } }

.feedback__fieldset {
  border: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin-top: 71px; }
  @media (max-width: 768px) {
    .feedback__fieldset {
      padding-left: 2px; } }

.feedback__legend {
  color: #fff;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em;
  padding: 0;
  margin-bottom: 18px; }
  @media (max-width: 768px) {
    .feedback__legend {
      font-size: 18px;
      color: #000;
      margin-bottom: 29px; } }

.feedback__input {
  width: 327px;
  height: 48px;
  border: 1px solid #c9ccd4;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 14px 29px 10px 29px; }
  .feedback__input::-moz-placeholder {
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.14; }
  .feedback__input::placeholder {
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.14; }
  @media (max-width: 768px) {
    .feedback__input {
      width: 321px;
      padding-top: 12px;
      padding-left: 22px; } }

.feedback__btn {
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000;
  background-color: #0ad9c6;
  padding: 16px 51px 15px 53px;
  border-radius: 6px; }
  @media (max-width: 768px) {
    .feedback__btn {
      padding: 16px 50px 15px 50px;
      margin-left: 2px; } }

.btn {
  border: none;
  cursor: pointer; }
